<template>
	<view>
		<view class="content">
			<u-toast ref="uToast"></u-toast>
			<!-- 标题 -->
			<view class="content-text">
				<view class="content-text-title">欢迎使用！</view>
				<view class="content-text-sketch">成功不是凭梦想和希望，而是凭努力和实践。</view>
			</view>
			<!-- 表单 start-->
			<view class="content-from">
				<view class="cu-form-group">
					<u-icon name="man-add" color="#409EFF" size="28"></u-icon>
					<u--input placeholder="*请输入姓名" shape="circle" clearable border="none" v-model="InfoSubmit.username">
					</u--input>
				</view>
				<view class="cu-form-group">
					<u-icon name="phone" color="#409EFF" size="28"></u-icon>
					<u--input placeholder="*请输入手机号" shape="circle" clearable border="none" v-model="InfoSubmit.mobile">
					</u--input>
				</view>
				<view class="cu-form-group">
					<u-icon name="eye" color="#409EFF" size="28"></u-icon>
					<u--input placeholder="*请输入密码" shape="circle" clearable border="none" v-model="InfoSubmit.password">
					</u--input>
				</view>
				<view class="cu-form-group">
					<u-icon name="eye" color="#409EFF" size="28"></u-icon>
					<u--input placeholder="*请输入确认密码" shape="circle" clearable border="none" v-model="InfoSubmit.qpassword">
					</u--input>
				</view>
				<view class="content-btn">
					<u-button type="primary" shape="circle" text="注册" @click="RegistSubmit"></u-button>
					<view class="forget-regist">
						<view @click="Loginto">已有账号？开始登录！</view>
					</view>
				</view>
			</view>
			<!-- 表单end -->
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				InfoSubmit: {
					"username": null,
					"mobile": null,
					"password": null,
					"qpassword":null,
				},
				show: false,
				columns: [
				],
			}
		},
		onLoad() {
		},
		methods: {
			RegistSubmit() {
				let data = this.InfoSubmit;
				if (data.username == null || data.mobile == null || data.password == null ) {
					this.$refs.uToast.show({
						message: "有字段未填写！",
					})
					return true;
				}
				if(data.password!=data.qpassword){
					this.$refs.uToast.show({
						message: "两次密码不一致！",
					})
					return true;
				}
					this.$request.request("/Regiest/regiest", data, "POST").then(res => {
						if (res.code == 200) {
							this.$refs.uToast.show({
								message: res.msg
							})
						} else {
							this.$refs.uToast.show({
								message: res.msg
							})
						}
					}).catch()
			},
			Loginto() {
				uni.navigateTo({
					url: '/pages/login/login'
				})
			},
		}
	}
</script>

<style>
	.content {
		width: 90%;
		margin: 0 auto;
		box-sizing: border-box;
		padding: 20% 0;
	}

	.content-text {
		padding: 40px 0px;
	}

	.content-text-title {
		font-size: 50rpx;
		font-weight: bold;
		letter-spacing: 10rpx;
	}

	.content-text-sketch {
		margin-top: 20rpx;
		letter-spacing: 5rpx;
	}

	.cu-form-group {
		display: flex;
		justify-content: flex-start;
		margin-top: 15px;
		border-radius: 20px;
		height: 45px;
		background: #f9f9f9;
		box-shadow: inset 5px 5px 10px #dedede,
			inset -5px -5px -10px #ffffff;
	}

	.cu-form-group .u-icon {
		margin: 0px 10px;
	}

	.content-btn {
		margin-top: 50rpx;
		display: flex;
		flex-wrap: wrap;
	}

	.content-btn button {
		letter-spacing: 10rpx;
		margin-right: 2%;
		background-color: #409EFF;
		border: none;
	}

	.forget-regist {
		width: 95%;
		margin-left: 2.5%;
		margin-top: 15px;
		display: flex;
		justify-content: flex-end;
		color: #B9B9B9;
	}

	.forget-regist view {
		color: #409EFF;
	}

	/*  */
	.login-other {
		margin-top: 100rpx;
	}

	.login-other-text {
		text-align: center;
		letter-spacing: 6rpx;
		color: #B9B9B9;
	}

	.login-other-icon {
		margin-top: 40rpx;
		display: flex;
		justify-content: center;
	}

	.login-other-icon .u-icon {
		font-size: 60rpx;
		margin-right: 30rpx;
	}

	.u-input {
		padding-right: 10px !important;
	}
</style>
